  --container-offset-high-tablet: 27px;
  --container-offset-small-tablet: 21px;
  --white-color: #fff;
  --black-color: #202020;
  --accent-color: #FF6E30;
  --brown-color: #BC572B;
  --red-color: #CE4307;
  --light-black-color: #4A4A4A;
  --grey-color: #999;
  --map-bg-color: rgba(32, 32, 32, 0.85);
  --grey-text-color: #CACACA;
  --input-request-color: #E9E9E9;
  --close-icon-color: #808080;
  --error-color: #F06666;
}

